import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			form:{
				incomeList: [],// 收益列表
				monthAmount: '',//当月收入 
				monthtxAmount: '',//当月所提现 
				notRecordedAmount: '',
				totalAmount: '',//累计收入
				totalactulAmount: '',//累计实际提现
				yyAmount: ''//账户余额
			},
			page:{
				current:1,
				pageSize:10
			}
		}
	},
	onLoad(){
		this.getData()
	},
	methods:{
		
		getData(){
			let obj = {
				pageNum:this.page.current,
				pageSize:this.page.pageSize
			};
			axios.post('/income/list',obj).then(res=>{
				this.form = res.data.data
			})
		}
	}
}
